.case-header {
    &-content {
        display: flex;
        flex-flow: column;
        justify-content: space-between;
        height: 980px;
        margin: 0 auto;
        max-width: 1730px;

        .case-nav {
            width: 100%;
            ul {
                display: flex;
                justify-content: space-between;
                align-items: center;
                width: 100%;
                padding: 40px 0 0 30px;

                li {
                    &:first-of-type {
                        svg {
                            width: 43px;
                            height: 41px;
                        }
                    }
                    &:last-of-type {
                        a {
                            position: relative;
                            font-size: 1.5rem;
                            color: #ffffff;    
                            opacity: 0.7;
                            transition: opacity 0.2s;
                            &::before {
                                position: absolute;
                                top: -16px;
                                left: -70px;
                                font-size: 3rem;
                                line-height: 1;
                                opacity: 0.7;
                                content: '←'
                            }
                        }   
                        &:hover {
                            a {
                                opacity: 1;
                            }
                        &::before {
                            opacity: 1;
                        }
                    }                 
                }
            }
        }
    }
    &-text {
        color: #ffffff;
        h2 {
            font-size: 2.4rem;
            font-weight: 700;
            line-height: 3.7rem;
        }

        p { 
            font-size: 1.2rem;
            line-height: 2rem;

            &:first-of-type {
                margin-top: 35px;
            }
        }

        ul {
            margin-top: 40px;
            display: flex;

            li {
                margin-left: 50px;
                font-size: 1rem;
                opacity: 0.5;

                &:first-of-type {
                    margin-left: 0;
                }
            }
        }
    }
    .case-arrow-down {
        position: relative;
        margin-bottom: 50px;
        height: 50px;

        img {
            position: absolute;
            top: 0;
            left: 50%;
            width: 50px;
            height: 50px;
            border-radius: 50%;
            transform: translateX(-50%);          

            animation: bounce;
            animation-duration: 2s;
            animation-iteration-count: infinite;
            animation-timing-function: ease-in-out;
        }
    }
}
}

.case-main {
    .info-and-goal {
        box-sizing: content-box;
        display: flex;
        justify-content: space-between;
        max-width: 1520px;
        margin-top: 100px;
        padding-left: 100px;

        .pool-image {
            max-height: 780px;
            max-width: 40%;
            box-shadow: 0 12px 30px rgba(9, 2, 27, 0.15);
            border-radius: 15px;
            overflow: hidden;
            img {                
                width: auto;
                height: 100%;
            }
        }

        &-content {
            ul li {
                margin-top: 140px;

                &:first-of-type {
                    margin-top: 0;
                }

                h3 {
                    font-size: 2.4rem;
                    font-weight: 700;
                }

                p {
                    max-width: 650px;
                    margin-top: 30px;
                    font-size: 1.2rem;
                    line-height: 1.8rem;
                }
            }
        }
    }

    .case-solution {
        margin-top: 200px;
        padding-left: 100px;
        h3 {
            font-size: 2.4rem;
            font-weight: 700;
        }

        .case-solution-stage {
            margin-top: 100px;
            position: relative;
            // stage number
            & > span {
                position: absolute;
                top: 3px;
                left: 95px;
                font-size: 1.2rem;
                opacity: 0.5;
                font-weight: 700;
            }
        }
    }
}



.case-solution-block {
    margin: 40px 0 0 140px;
    max-width: 800px;

    p {
        font-size: 1.2rem;
        line-height: 1.8rem;
        font-weight: 400;
        span {
            font-weight: 700;
        }
    }
}
.case-other-inforamtion {
    margin-top: 150px;
    padding-left: 100px;
}

.case-solution-image {
    max-width: 100%;
    margin: 100px 0;
    position: relative;
    display: flex;                
    justify-content: center;                

    img {
        padding: 0 20px;
        max-width: 100%;
        height: 100%;
        width: auto;                    
    }
    .case-solution-image-title {
        position: absolute;
        left: 200px;
        bottom: -40px;
        font-size: 19px;
        opacity: 0.5;
    }
}

.case-quotes-block {
    box-sizing: content-box;
    padding: 150px 0 0 140px;
    max-width: 800px;
    background-repeat: no-repeat !important;
    background-position: 70px 90px !important;
    p {
        font-size: 1.8rem;
        font-weight: 700;
        line-height: 2.4rem;
    }
}

.header-for-stage {
    margin: 100px 0 0 140px;
    font-size: 1.6rem;
}

.workers {
    margin-top: 150px;
    padding-left: 100px;
    h2 {
        font-size: 1.8rem;
    }

    .workers-content {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
        max-width: 80%;
        width: 100%;
        margin: 100px auto 160px;

        .single-worker {
            display: flex;
            align-items: center;

            &-image {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 150px;
                height: 150px;
                overflow: hidden;
                border-radius: 50%;

                img {
                    width: 100%;
                    height: auto;
                }
            }

            &-info {
                margin-left: 35px;
                h4 {
                    max-width: 140px;
                    font-size: 1.8rem;
                    line-height: 2.2rem;
                    font-weight: 700;
                }
                p {
                    margin-top: 15px;
                    font-size: 1.1rem;
                }
            }
        }

        .case-workers-hashtags {
            position: absolute;
            bottom: -60px;
            left: 0;
            display: flex;
            
            li {
                margin-left: 50px;
                font-size: 1rem;
                opacity: 0.5;

                &:first-of-type {
                    margin: 0;
                }
            }
        }
    }
}

